<template>
  <div class="regist-card">
    <h3>{{ doctorName }}</h3>
    <p class="dept">{{ department }}</p>
    <div class="info">
      <span>{{ title }}</span>
      <span v-if="timeSlots && timeSlots.length"></span>
    </div>
    <div class="slot-select" v-if="timeSlots && timeSlots.length">
      <select v-model="selectedSlot" class="slot-input">
        <option disabled value=""></option>
        <option
          v-for="slot in timeSlots"
          :key="slot.slotId"
          :value="slot.slotId"
        >
          {{ formatSlot(slot) }}
        </option>
      </select>
    </div>
    <button
      class="btn"
      :class="{ disabled: !timeSlots || !timeSlots.length }"
      :disabled="!timeSlots || !timeSlots.length"
      @click="handleRegister"
    >
      {{ timeSlots && timeSlots.length ? '' : '' }}
    </button>
  </div>
</template>

<script>
export default {
  // 
  props: {
    doctorName: String,
    department: String,
    title: String,
    timeSlots: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      selectedSlot: ''
    }
  },
  methods: {
    formatSlot (slot) {
      //  label 
      if (!slot) return ''
      if (slot.label) return slot.label + ` ${slot.remain}`
      if (slot.startTime && slot.endTime) {
        return `${slot.startTime}-${slot.endTime} ${slot.remain}`
      }
      return ` ${slot.remain}`
    },
    handleRegister () {
      if (!this.timeSlots || !this.timeSlots.length) {
        alert('')
        return
      }
      if (!this.selectedSlot) {
        alert('')
        return
      }
      const chosen = this.timeSlots.find(s => s.slotId === this.selectedSlot)
      this.$emit('register', chosen)
    }
  }
}
</script>

