<template>
  <div class="doctor-panel">
    <h2></h2>
    <p class="tip"></p>

    <div class="reg-section">
      <h3></h3>
      <div class="reg-filter">
        <label></label>
        <input v-model="regDate" type="date" class="input">
        <button class="btn small" @click="loadRegistrations"></button>
      </div>
      <table class="reg-table" v-if="registrations.length">
        <thead>
          <tr>
            <th></th>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="reg in registrations" :key="reg.regId">
            <td>{{ reg.regId }}</td>
            <td>{{ reg.patientId }}</td>
            <td>{{ reg.patientName }}</td>
            <td>{{ reg.regDate }}</td>
            <td>{{ reg.regTimeSlot }}</td>
            <td>{{ reg.regStatus }}</td>
            <td>{{ reg.payStatus }}</td>
            <td>
              <button class="btn small" @click="fillTarget(reg.patientId)"></button>
              <button class="btn small" @click="openRecordForm(reg)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <div v-else class="empty-tip"></div>
    </div>

    <div class="form">
      <div class="form-item">
        <label>ID</label>
        <input v-model="targetUserId" type="text" placeholder=" UserID" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="title" type="text" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <textarea v-model="content" rows="4" placeholder="" class="textarea"></textarea>
      </div>
      <button class="btn" @click="sendMessage"></button>
    </div>

    <div v-if="showRecordForm" class="form">
      <h3></h3>
      <div class="form-item">
        <label></label>
        <span>{{ recordForm.regId }}</span>
      </div>
      <div class="form-item">
        <label></label>
        <span>{{ recordForm.patientName }}{{ recordForm.patientId }}</span>
      </div>
      <div class="form-item">
        <label></label>
        <textarea v-model="recordForm.diagnosis" rows="3" class="textarea" />
      </div>
      <div class="form-item">
        <label></label>
        <textarea v-model="recordForm.treatmentPlan" rows="3" class="textarea" />
      </div>
      <div class="form-item">
        <label></label>
        <textarea v-model="recordForm.advice" rows="3" class="textarea" />
      </div>
      <button class="btn" @click="saveRecord"></button>
      <button class="btn small" style="margin-left: 10px;" @click="showRecordForm = false"></button>
    </div>

    <h3 class="sub-title"></h3>
    <div class="msg-list">
      <MessageCard
        v-for="msg in outbox"
        :key="msg.messageId"
        :title="msg.title"
        :content="msg.content"
        :time="formatTime(msg.createTime)"
      />
      <div v-if="!outbox.length" class="empty-tip"></div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import MessageCard from '@/components/MessageCard'

export default {
  components: {
    MessageCard
  },
  data () {
    return {
      regDate: '',
      registrations: [],
      targetUserId: '',
      title: '',
      content: '',
      outbox: [],
      showRecordForm: false,
      recordForm: {
        regId: '',
        patientId: '',
        patientName: '',
        diagnosis: '',
        treatmentPlan: '',
        advice: ''
      }
    }
  },
  created () {
    const today = new Date()
    this.regDate = today.toISOString().slice(0, 10)
    this.loadRegistrations()
    this.loadOutbox()
  },
  methods: {
    async loadRegistrations () {
      try {
        const res = await api.get('/doctor/registrations', {
          params: { date: this.regDate || null }
        })
        const body = res.data
        if (body.error_code === 0) {
          this.registrations = body.data || []
        } else {
          this.registrations = []
        }
      } catch (e) {
        console.error(e)
        this.registrations = []
      }
    },
    fillTarget (patientId) {
      this.targetUserId = patientId
    },
    openRecordForm (reg) {
      this.recordForm = {
        regId: reg.regId,
        patientId: reg.patientId,
        patientName: reg.patientName,
        diagnosis: '',
        treatmentPlan: '',
        advice: ''
      }
      this.showRecordForm = true
    },
    async sendMessage () {
      if (!this.targetUserId || !this.title || !this.content) {
        alert('')
        return
      }
      try {
        const res = await api.post('/messages', {
          targetUserId: this.targetUserId,
          title: this.title,
          content: this.content
        })
        const body = res.data
        if (body.error_code === 0) {
          alert('')
          this.title = ''
          this.content = ''
          this.loadOutbox()
        } else {
          alert(body.msg || '')
        }
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async loadOutbox () {
      try {
        const res = await api.get('/messages', {
          params: { box: 'outbox' }
        })
        const body = res.data
        if (body.error_code === 0) {
          this.outbox = body.data || []
        }
      } catch (e) {
        console.error(e)
      }
    },
    async saveRecord () {
      if (!this.recordForm.regId || !this.recordForm.diagnosis) {
        alert('')
        return
      }
      try {
        const res = await api.post('/medical-records', {
          regId: this.recordForm.regId,
          diagnosis: this.recordForm.diagnosis,
          treatmentPlan: this.recordForm.treatmentPlan,
          advice: this.recordForm.advice
        })
        const body = res.data
        if (body.error_code === 0) {
          alert('')
          this.showRecordForm = false
        } else {
          alert(body.msg || '')
        }
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    formatTime (t) {
      if (!t) return ''
      return String(t).replace('T', ' ').substring(0, 16)
    }
  }
}
</script>

