<template>
  <div id="app">
    <!--  -->
    <nav class="nav-bar">
      <router-link v-if="!isLoggedIn" to="/login-register" class="nav-item">/</router-link>
      <router-link v-if="isLoggedIn && isPatient" to="/registration" class="nav-item"></router-link>
      <router-link v-if="isLoggedIn" to="/message" class="nav-item"></router-link>
      <router-link v-if="isLoggedIn" to="/profile" class="nav-item"></router-link>
      <router-link v-if="isLoggedIn && isDoctor" to="/doctor-panel" class="nav-item"></router-link>
      <router-link v-if="isLoggedIn && isDeptManager" to="/dept-panel" class="nav-item"></router-link>
      <router-link v-if="isLoggedIn && isAdmin" to="/admin-panel" class="nav-item"></router-link>
    </nav>
    <!--  -->
    <div class="page-container">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userInfo: null
    }
  },
  computed: {
    isLoggedIn () {
      return !!this.userInfo
    },
    isPatient () {
      return this.userInfo && this.userInfo.role === 'patient'
    },
    isDoctor () {
      return this.userInfo && this.userInfo.role === 'doctor'
    },
    isDeptManager () {
      return this.userInfo && this.userInfo.role === 'dept_manager'
    },
    isAdmin () {
      return this.userInfo && this.userInfo.role === 'admin'
    }
  },
  created () {
    this.refreshUserInfo()
  },
  watch: {
    $route () {
      this.refreshUserInfo()
    }
  },
  methods: {
    refreshUserInfo () {
      const infoStr = localStorage.getItem('userInfo')
      if (infoStr) {
        try {
          this.userInfo = JSON.parse(infoStr)
        } catch (e) {
          this.userInfo = null
        }
      } else {
        this.userInfo = null
      }
    }
  }
}
</script>

