<template>
  <div class="registration-page">
    <h2></h2>
    <div class="dept-select">
      <label></label>
      <select v-model="selectedDeptId" class="select">
        <option value=""></option>
        <option v-for="dept in departmentList" :key="dept.departmentId" :value="dept.departmentId">
          {{ dept.departmentName }}{{ dept.location }}
        </option>
      </select>
    </div>
    <div class="date-select">
      <label></label>
      <input v-model="selectedDate" type="date" class="select">
    </div>
    <!--  -->
    <div class="card-list">
      <RegistrationCard 
        v-for="doctor in doctorList"
        :key="doctor.userId"
        :doctorName="doctor.doctorName"
        :department="getDeptName(doctor.departmentId)"
        :title="doctor.title"
        :timeSlots="doctor.availableSlots"
        @register="slot => handleRegister(doctor, slot)"
      />
      <div v-if="doctorList.length === 0" class="empty-tip">
        
      </div>
    </div>
  </div>
</template>

<script>
// 
import RegistrationCard from '@/components/RegistrationCard'
import api from '@/api'

export default {
  components: {
    RegistrationCard
  },
  data() {
    return {
      departmentList: [],
      selectedDeptId: '',
      selectedDate: '',
      doctorList: []
    }
  },
  created () {
    this.loadDepartments()
  },
  watch: {
    selectedDeptId () {
      this.loadDoctors()
    },
    selectedDate () {
      this.loadDoctors()
    }
  },
  methods: {
    async loadDepartments () {
      try {
        const res = await api.get('/departments')
        const body = res.data
        if (body.error_code === 0) {
          this.departmentList = body.data || []
        }
      } catch (e) {
        console.error(e)
      }
    },
    async loadDoctors () {
      if (!this.selectedDeptId || !this.selectedDate) {
        this.doctorList = []
        return
      }
      try {
        const res = await api.get('/doctors', {
          params: {
            departmentId: this.selectedDeptId,
            date: this.selectedDate
          }
        })
        const body = res.data
        if (body.error_code === 0) {
          this.doctorList = body.data || []
        } else {
          this.doctorList = []
        }
      } catch (e) {
        console.error(e)
        this.doctorList = []
      }
    },
    async handleRegister (doctor, timeSlot) {
      if (!this.selectedDate || !this.selectedDeptId) {
        alert('')
        return
      }
      if (!timeSlot || !timeSlot.slotId) {
        alert('')
        return
      }
      try {
        const res = await api.post('/registrations', {
          doctorId: doctor.userId,
          departmentId: this.selectedDeptId,
          regDate: this.selectedDate,
          slotId: timeSlot.slotId
        })
        const body = res.data
        if (body.error_code === 0) {
          alert('' + body.data.regId)
        } else {
          alert(body.msg || '')
        }
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    getDeptName (deptId) {
      const dept = this.departmentList.find(d => d.departmentId === deptId)
      return dept ? dept.departmentName : ''
    }
  }
}
</script>

<style scoped>
.registration-page h2 {
  color: #2f5496;
  margin-bottom: 20px;
}
.dept-select {
  margin-bottom: 20px;
}
.date-select {
  margin-bottom: 20px;
}
.select {
  padding: 8px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  margin-left: 10px;
}
.card-list {
  margin-top: 20px;
}
.empty-tip {
  margin-top: 10px;
  color: #777;
}
</style>
