<template>
  <div class="admin-panel">
    <h2></h2>
    <p class="tip"> ID  /  /  / </p>

    <div class="form">
      <div class="form-item">
        <label>ID</label>
        <input v-model="userId" type="text" placeholder="P2023... / DR0001" class="input">
      </div>
      <div class="btn-group">
        <button class="btn freeze" @click="freezeUser"></button>
        <button class="btn unfreeze" @click="unfreezeUser"></button>
      </div>
      <p v-if="resultMsg" class="result">{{ resultMsg }}</p>
    </div>

    <div class="form create-form">
      <h3></h3>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.doctorName" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.phone" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.password" type="password" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <select v-model="doctorForm.departmentId" class="input">
          <option value=""></option>
          <option v-for="d in departments" :key="d.departmentId" :value="d.departmentId">
            {{ d.departmentName }}{{ d.departmentId }}
          </option>
        </select>
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.title" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.specialty" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="doctorForm.workPhone" type="text" class="input">
      </div>
      <button class="btn create" @click="createDoctor"></button>
    </div>

    <div class="form create-form">
      <h3></h3>
      <div class="form-item">
        <label></label>
        <input v-model="deptForm.userName" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="deptForm.phone" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="deptForm.password" type="password" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <select v-model="deptForm.departmentId" class="input">
          <option value=""></option>
          <option v-for="d in departments" :key="d.departmentId" :value="d.departmentId">
            {{ d.departmentName }}{{ d.departmentId }}
          </option>
        </select>
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="deptForm.workPhone" type="text" class="input">
      </div>
      <button class="btn create" @click="createDeptManager"></button>
    </div>

    <div class="form create-form">
      <h3></h3>
      <div class="form-item">
        <label></label>
        <input v-model="sysForm.userName" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="sysForm.phone" type="text" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="sysForm.password" type="password" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="sysForm.workPhone" type="text" class="input">
      </div>
      <button class="btn create" @click="createSystemUser"></button>
    </div>
  </div>
</template>

<script>
import api from '@/api'

export default {
  data () {
    return {
      userId: '',
      resultMsg: '',
      departments: [],
      doctorForm: {
        doctorName: '',
        phone: '',
        password: '',
        departmentId: '',
        title: '',
        specialty: '',
        workPhone: ''
      },
      deptForm: {
        userName: '',
        phone: '',
        password: '',
        departmentId: '',
        workPhone: ''
      },
      sysForm: {
        userName: '',
        phone: '',
        password: '',
        workPhone: ''
      }
    }
  },
  created () {
    this.loadDepartments()
  },
  methods: {
    async loadDepartments () {
      try {
        const res = await api.get('/departments')
        const body = res.data
        if (body.error_code === 0) {
          this.departments = body.data || []
        }
      } catch (e) {
        console.error(e)
      }
    },
    async freezeUser () {
      if (!this.userId) {
        alert('ID')
        return
      }
      try {
        const res = await api.post(`/users/${this.userId}/freeze`)
        const body = res.data
        this.resultMsg = body.msg || ''
      } catch (e) {
        console.error(e)
        this.resultMsg = 'ID'
      }
    },
    async unfreezeUser () {
      if (!this.userId) {
        alert('ID')
        return
      }
      try {
        const res = await api.post(`/users/${this.userId}/unfreeze`)
        const body = res.data
        this.resultMsg = body.msg || ''
      } catch (e) {
        console.error(e)
        this.resultMsg = 'ID'
      }
    },
    async createDoctor () {
      const f = this.doctorForm
      if (!f.doctorName || !f.phone || !f.password || !f.departmentId) {
        alert('ID')
        return
      }
      try {
        const res = await api.post('/users/doctors', f)
        const body = res.data
        alert(body.msg || '')
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async createDeptManager () {
      const f = this.deptForm
      if (!f.userName || !f.phone || !f.password || !f.departmentId) {
        alert('ID')
        return
      }
      try {
        const res = await api.post('/users/dept-managers', f)
        const body = res.data
        alert(body.msg || '')
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async createSystemUser () {
      const f = this.sysForm
      if (!f.userName || !f.phone || !f.password) {
        alert('')
        return
      }
      try {
        const res = await api.post('/users/system-users', f)
        const body = res.data
        alert(body.msg || '')
      } catch (e) {
        console.error(e)
        alert('')
      }
    }
  }
}
</script>

