<template>
  <div class="login-register">
    <div class="tab-header">
      <div class="tab-item" :class="{ active: isLogin }" @click="isLogin = true"></div>
      <div class="tab-item" :class="{ active: !isLogin }" @click="isLogin = false"></div>
    </div>
    <!--  -->
    <div class="form-container" v-if="isLogin">
      <div class="form-item">
        <label></label>
        <input v-model="loginId" type="tel" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="loginPassword" type="password" placeholder="" class="input">
      </div>
      <button class="submit-btn" @click="handleLogin"></button>
    </div>
    <!--  -->
    <div class="form-container" v-else>
      <div class="form-item">
        <label></label>
        <input v-model="registerForm.patientName" type="text" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <select v-model="registerForm.gender" class="input">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>
      <div class="form-item">
        <label></label>
        <input v-model.number="registerForm.age" type="number" min="0" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="registerForm.phone" type="tel" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="registerForm.password" type="password" placeholder="" class="input">
      </div>
      <div class="form-item">
        <label></label>
        <input v-model="registerForm.idCard" type="text" placeholder="" class="input">
      </div>
      <button class="submit-btn" @click="handleRegister"></button>
    </div>
  </div>
</template>

<script>
import api from '@/api'

export default {
  data() {
    return {
      isLogin: true, // 
      loginId: '',
      loginPassword: '',
      registerForm: {
        patientName: '',
        gender: '',
        age: null,
        phone: '',
        password: '',
        idCard: ''
      }
    }
  },
  methods: {
    async handleLogin () {
      if (!this.loginId || !this.loginPassword) {
        alert('')
        return
      }
      try {
        const res = await api.post('/auth/login', {
          id: this.loginId,
          password: this.loginPassword
        })
        const body = res.data
        if (body.error_code !== 0) {
          alert(body.msg || '')
          return
        }
        const data = body.data
        localStorage.setItem('token', data.token)
        localStorage.setItem('userInfo', JSON.stringify(data))
        // 
        this.$router.push('/profile')
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async handleRegister () {
      const f = this.registerForm
      if (!f.patientName || !f.gender || !f.age || !f.phone || !f.password || !f.idCard) {
        alert('')
        return
      }
      try {
        const res = await api.post('/auth/patient/register', f)
        const body = res.data
        if (body.error_code !== 0) {
          alert(body.msg || '')
          return
        }
        alert('')
        this.isLogin = true
        this.loginId = f.phone
      } catch (e) {
        console.error(e)
        alert('')
      }
    }
  }
}
</script>

