<template>
  <div class="dept-panel">
    <h2></h2>
    <p class="tip">
      
    </p>

    <!--  -->
    <section class="block">
      <h3></h3>
      <div class="assign-bar">
        <label></label>
        <input v-model="assignDoctorId" type="text" class="input" placeholder=" UserID DR0001">
        <button class="btn small" @click="assignDoctor"></button>
      </div>
      <table class="table" v-if="doctors.length">
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="d in doctors" :key="d.userId">
            <td>{{ d.userId }}</td>
            <td>{{ d.doctorName }}</td>
            <td>{{ d.title }}</td>
          </tr>
        </tbody>
      </table>
      <div v-else class="empty-tip"></div>
    </section>

    <!--  -->
    <section class="block">
      <h3></h3>
      <div class="form-row">
        <label></label>
        <select v-model="slotForm.doctorId" class="input">
          <option value=""></option>
          <option v-for="d in doctors" :key="d.userId" :value="d.userId">
            {{ d.doctorName }}{{ d.userId }}
          </option>
        </select>
      </div>
      <div class="form-row">
        <label></label>
        <input v-model="slotForm.slotDate" type="date" class="input">
      </div>
      <div class="form-row">
        <label></label>
        <input v-model="slotForm.startTime" type="time" class="input">
      </div>
      <div class="form-row">
        <label></label>
        <input v-model="slotForm.endTime" type="time" class="input">
      </div>
      <div class="form-row">
        <label></label>
        <input v-model.number="slotForm.capacity" type="number" min="1" class="input">
      </div>
      <div class="form-row">
        <label></label>
        <input v-model="slotForm.note" type="text" class="input" placeholder=" ">
      </div>
      <button class="btn" @click="createSlot"></button>

      <h3 style="margin-top: 24px;"></h3>
      <div class="form-row">
        <label></label>
        <input v-model="slotQueryDate" type="date" class="input">
        <button class="btn small" @click="loadSlots"></button>
      </div>
      <table class="table" v-if="slots.length">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="s in slots" :key="s.slotId">
            <td>{{ s.doctorName }}{{ s.doctorId }}</td>
            <td>{{ s.slotDate }}</td>
            <td>{{ formatSlotTime(s) }}</td>
            <td>{{ s.capacity }}</td>
            <td>{{ s.bookedCount }}</td>
            <td>{{ s.status }}</td>
            <td>{{ s.note }}</td>
          </tr>
        </tbody>
      </table>
      <div v-else class="empty-tip"></div>
    </section>

    <!--  -->
    <section class="block">
      <h3></h3>
      <div class="form-row">
        <label></label>
        <input v-model="regQueryDate" type="date" class="input">
        <button class="btn small" @click="loadRegistrations"></button>
      </div>
      <table class="table" v-if="registrations.length">
        <thead>
          <tr>
            <th></th>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="r in registrations" :key="r.regId">
            <td>{{ r.regId }}</td>
            <td>{{ r.patientId }}</td>
            <td>{{ r.patientName }}</td>
            <td>{{ r.doctorName }}{{ r.doctorId }}</td>
            <td>{{ r.regDate }}</td>
            <td>{{ r.regTimeSlot }}</td>
            <td>{{ r.regStatus }}</td>
            <td>{{ r.payStatus }}</td>
            <td>
              <button class="btn small" @click="fillTarget(r.patientId)"></button>
              <button class="btn small" style="margin-left:6px;" @click="fillTarget(r.doctorId)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <div v-else class="empty-tip"></div>
    </section>

    <!--  -->
    <section class="block">
      <h3></h3>
      <div class="form-row">
        <label>ID</label>
        <input v-model="targetUserId" type="text" class="input" placeholder=" UserID">
      </div>
      <div class="form-row">
        <label></label>
        <input v-model="title" type="text" class="input">
      </div>
      <div class="form-row">
        <label></label>
        <textarea v-model="content" rows="3" class="textarea" />
      </div>
      <button class="btn" @click="sendMessage"></button>
    </section>
  </div>
</template>

<script>
import api from '@/api'

export default {
  data () {
    return {
      doctors: [],
      assignDoctorId: '',
      slotForm: {
        doctorId: '',
        slotDate: '',
        startTime: '',
        endTime: '',
        capacity: 1,
        note: ''
      },
      slotQueryDate: '',
      slots: [],
      regQueryDate: '',
      registrations: [],
      targetUserId: '',
      title: '',
      content: ''
    }
  },
  created () {
    const today = new Date().toISOString().slice(0, 10)
    this.slotQueryDate = today
    this.regQueryDate = today
    this.loadDoctors()
    this.loadSlots()
    this.loadRegistrations()
  },
  methods: {
    async loadDoctors () {
      try {
        const res = await api.get('/dept/doctors')
        const body = res.data
        if (body.error_code === 0) {
          this.doctors = body.data || []
        } else {
          this.doctors = []
        }
      } catch (e) {
        console.error(e)
        this.doctors = []
      }
    },
    async assignDoctor () {
      if (!this.assignDoctorId) {
        alert('ID')
        return
      }
      try {
        const res = await api.post(`/dept/doctors/${this.assignDoctorId}/assign`)
        const body = res.data
        alert(body.msg || '')
        this.assignDoctorId = ''
        this.loadDoctors()
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async createSlot () {
      const f = this.slotForm
      if (!f.doctorId || !f.slotDate || !f.startTime || !f.endTime || !f.capacity) {
        alert('')
        return
      }
      try {
        const res = await api.post('/dept/slots', f)
        const body = res.data
        alert(body.msg || '')
        this.loadSlots()
      } catch (e) {
        console.error(e)
        alert('')
      }
    },
    async loadSlots () {
      try {
        const res = await api.get('/dept/slots', {
          params: {
            date: this.slotQueryDate || null
          }
        })
        const body = res.data
        if (body.error_code === 0) {
          this.slots = body.data || []
        } else {
          this.slots = []
        }
      } catch (e) {
        console.error(e)
        this.slots = []
      }
    },
    async loadRegistrations () {
      try {
        const res = await api.get('/dept/registrations', {
          params: {
            date: this.regQueryDate || null
          }
        })
        const body = res.data
        if (body.error_code === 0) {
          this.registrations = body.data || []
        } else {
          this.registrations = []
        }
      } catch (e) {
        console.error(e)
        this.registrations = []
      }
    },
    formatSlotTime (s) {
      if (!s) return ''
      const start = s.startTime ? String(s.startTime).substring(0, 5) : ''
      const end = s.endTime ? String(s.endTime).substring(0, 5) : ''
      return start && end ? `${start}-${end}` : ''
    },
    fillTarget (userId) {
      this.targetUserId = userId
    },
    async sendMessage () {
      if (!this.targetUserId || !this.title || !this.content) {
        alert('')
        return
      }
      try {
        const res = await api.post('/messages', {
          targetUserId: this.targetUserId,
          title: this.title,
          content: this.content
        })
        const body = res.data
        if (body.error_code === 0) {
          alert('')
          this.title = ''
          this.content = ''
        } else {
          alert(body.msg || '')
        }
      } catch (e) {
        console.error(e)
        alert('')
      }
    }
  }
}
</script>

