import Vue from 'vue'
import Router from 'vue-router'
//  
import LoginRegister from '@/views/LoginRegister'
import RegistrationPage from '@/views/RegistrationPage' // 
import MessagePage from '@/views/MessagePage' // 
import ProfilePage from '@/views/ProfilePage' // 
import DoctorPanel from '@/views/DoctorPanel'
import AdminPanel from '@/views/AdminPanel'
import DeptPanel from '@/views/DeptPanel'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: () => {
        const infoStr = localStorage.getItem('userInfo')
        return infoStr ? '/profile' : '/login-register'
      }
    },
    {
      path: '/login-register',
      name: 'LoginRegister',
      component: LoginRegister
    },
    {
      path: '/registration',
      name: 'RegistrationPage', // 
      component: RegistrationPage, // 
      meta: { roles: ['patient'] }
    },
    {
      path: '/message',
      name: 'MessagePage', // 
      component: MessagePage // 
    },
    {
      path: '/profile',
      name: 'ProfilePage', // 
      component: ProfilePage // 
    },
    {
      path: '/doctor-panel',
      name: 'DoctorPanel',
      component: DoctorPanel,
      meta: { roles: ['doctor'] }
    },
    {
      path: '/admin-panel',
      name: 'AdminPanel',
      component: AdminPanel,
      meta: { roles: ['admin'] }
    },
    {
      path: '/dept-panel',
      name: 'DeptPanel',
      component: DeptPanel,
      meta: { roles: ['dept_manager'] }
    }
  ]
})

router.beforeEach((to, from, next) => {
  const userStr = localStorage.getItem('userInfo')
  const user = userStr ? JSON.parse(userStr) : null
  const roles = to.meta && to.meta.roles
  const isPublic = to.path === '/login-register'

  // /
  if (!user && !isPublic) {
    return next('/login-register')
  }

  // /
  if (user && to.path === '/login-register') {
    return next('/profile')
  }

  // 
  if (roles && roles.length) {
    if (!user || !roles.includes(user.role)) {
      alert('')
      return next('/login-register')
    }
  }
  next()
})

export default router
